import React, {Component} from 'react';
import logo from './logo.svg';
import './Styles/css/App.css';
import './Styles/css/normalize.css';
import './Styles/css/RWD.css';
import Sidebar from "./Components/Sidebar";
import Viewer from "./Components/Viewer";
import {BrowserRouter as Router} from "react-router-dom";
class App extends Component {
    constructor(props) {
        super(props);
        this.state = {activeCategory: "HOME"};

    }

    handleUpdate(activeCategory) {
        this.setState({activeCategory: activeCategory});
    }

    render() {
        return (
            <Router>
                <div className="app">
                    <Sidebar updateActiveCategory={(actCategory) => this.handleUpdate(actCategory)}/>
                    <Viewer activeCategory={this.state.activeCategory}/>
                </div>
            </Router>
        );
    }
}

export default App;



