import React, {Component} from 'react';
import '../Styles/css/Sidebar.css';
import '../Styles/Fontello/css/fontello.css';
import {Link} from "react-router-dom";
import Home from "./Home";


class Sidebar extends Component {
    constructor(props) {
        super(props)
        this.handleClick = this.handleClick.bind(this);
        this.state = {activeCategory: "HOME"};
    }

    handleClick(evt) {
        const activeCategory = evt.target.getAttribute('data-name');
        this.setState = {activeCategory: activeCategory};
        this.props.updateActiveCategory(activeCategory);
    }

    render() {
        return (
            <nav>
                <div className="sidebar">
                    <ul className="sidebar-list">
                        <div className="sidebar-menu">
                            <li className="sidebar-logo"><Link to="/home"><span>S</span></Link></li>
                            <li><Link to="/home" data-name="HOME">
                                <span
                                    className="icon-home"></span></Link>
                            </li>
                            <li><Link to="/about" data-name="ABOUT"><span
                                className="icon-user"></span></Link>
                            </li>
                            <li><Link to="/skills" data-name="SKILLS"><span
                                className="icon-cog"></span></Link>
                            </li>
                            <li><Link to="/projects" data-name="PROJECTS"><span
                                className="icon-folder-open"></span></Link></li>
                            <li><Link to="/contact" data-name="CONTACT"><span

                                className="icon-mail"></span></Link>
                            </li>
                        </div>

                        <div className="sidebar-social">

                            <li className="sidebar-small"><a href="https://www.linkedin.com/in/karol-gruszka-34ab32149/"
                                                             data-name="LinkedIn"><span
                                className="icon-linkedin"></span></a>
                            </li>

                            <li className="sidebar-small"><a href="#" data-name="CV"><span
                                className=" icon-down-circled2"></span></a>
                            </li>

                            <li className="sidebar-small"><a href="https://github.com/Sniadekk" data-name="GitHub"><span
                                className="icon-github-circled"></span></a>
                            </li>
                        </div>
                    </ul>
                </div>
            </nav>
        );
    }

}

export default Sidebar;


