import React, {Component} from 'react';
import '../Styles/css/Viewer.css';
import Home from "./Home";
import About from "./About";
import Skills from "./Skills";
import Projects from "./Projects";
import Contact from "./Contact";
import {Route} from 'react-router-dom';



class Viewer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            style: {
                opacity: 1,
                transition: "all 0.3s linear",
            }
        };

        this.animateTransition = this.animateTransition.bind(this);


    }

    componentWillReceiveProps(props){
        console.log(props);
    }

    animateTransition() {
        this.setState({style:{opacity: 0, transition:"none"}});
        setTimeout(() => this.setState({style:{opacity: 1}}), 300);
    }

    render() {
        return (
            <div style={this.state.style} className="viewer">
                <Route exact path="/" component={Home}/>
                <Route path="/home" component={Home} />
                <Route path="/about" component={About}/>
                <Route path="/skills" component={Skills}/>
                <Route path="/projects" component={Projects}/>
                <Route path="/contact" component={Contact}/>
            </div>
        );
    }

}

export default Viewer;


