import React, {Component} from 'react';
import '../Styles/css/Projects.css';

class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {
            projects: [
                {
                    name: "Tracker",
                    source: "/Images/tracker.png",
                    github: "https://github.com/Sniadekk/ITrack",
                    description: "Application made to easily check what items you own."
                },
                {
                    name:"model-visualiser",
                    source: "/Images/visualiser.png",
                    github: "https://github.com/Sniadekk/product-visualizer",
                    description :"Application to allow 360 view of 3D model and also to allow user interact with it and change it's materials."
                },
                {
                    name:"portfolio",
                    source: "/Images/portfolio.png",
                    github: "https://github.com/Sniadekk/sniadek-website",
                    description: "Source code of my portfolio written entirely in React."
                }

            ]
        };

        this.parseItems.bind(this);
    }

    parseItems() {
        return this.state.projects.map((item) => <div key={item.name} className="projects-container-item">
                <img src={item.source} alt={item.description}/>
                <a href={item.github}><span className="icon-github-circled"></span></a>
            </div>
        )
    }


    render() {
        return (
            <div className="projects-container">
                {this.parseItems()}
            </div>
        );
    }

}


export default Projects;


