import React, {Component} from 'react';
import '../Styles/css/Contact.css';

class Contact extends Component {
    constructor(props) {
        super(props);
        this.maxLength = 200;


        this.handleChange = this.handleChange.bind(this);
        this.parseMessage = this.parseMessage.bind(this);
        this.state = {
            name: "",
            email: "",
            subject: "",
            message: ""
        }
    }

    handleChange(evt) {
        let value = evt.target.value;
        const name = evt.target.name;
        this.setState({[name]: value});
    }

    parseMessage() {
        if (this.state.message.length > this.maxLength) {
            return this.state.message.slice(0, this.maxLength);
        }

        else {
            return this.state.message.slice(0, this.state.message.length);
        }
    }

    render() {
        return (
            <div className="contact-container">
                <h1>Contact me</h1>
                <form action="https://formspree.io/karolgruszka9@gmail.com"
                      method="POST">
                    <div>
                        <label htmlFor="name">
                            Name
                        </label>
                        <input name="name" value={this.state.name} onChange={this.handleChange} id="name"
                               type="text" placeholder="Your name and surname." maxLength="100" autoCorrect="off"
                               autoCapitalize="words" required/>
                    </div>
                    <div>
                        <label htmlFor="email">
                            Email
                        </label>
                        <input name="email" value={this.state.email} onChange={this.handleChange} autoCorrect="off"
                               autoCapitalize="off" type="email" maxLength="255"
                               placeholder="Your contact email." required/>
                    </div>
                    <div>
                        <label htmlFor="subject">
                            Subject
                        </label>
                        <input name="subject" value={this.state.subject} onChange={this.handleChange} autoCorrect="on"
                               autoCapitalize="words" type="text" maxLength="150"
                               placeholder="What would you like me to do?" required/>
                    </div>
                    <div>
                        <label htmlFor="message">
                            Message
                        </label>
                        <textarea name="message" value={this.state.message} autoCorrect="on" autoCapitalize="words"
                                  onChange={this.handleChange}
                                  placeholder="Type in here." maxLength="1000" required/>
                    </div>

                    <input type="submit" value="SEND"/>
                </form>

                <div className="contact-email">
                    <div className="contact-email-item"> To: karolgruszka9@gmail.com</div>
                    <div className="contact-email-item">From: {this.state.name} </div>
                    <div className="contact-email-item">Subject:{this.state.subject} </div>
                    Message:
                    <div className="contact-email-item">{this.parseMessage()}...</div>
                </div>
            </div>
        );
    }
}


export default Contact;


