import React, {Component} from 'react';
import '../Styles/css/About.css';

class About extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <main>
                <div className="about-container">
                    <section>
                        <h1>About me</h1>
                        <p className="about-text">My passion to programming and software creation started in the second
                            grade of high school.</p>
                        <p className="about-text">I have started small. From creating static web pages to fully
                            responsive, dynamic applications supported by databases.</p>
                        <p className="about-text">Currently in free time, I'm learning alghoritms, electronics and
                            pushing my career towards robotics. </p>
                        <p className="about-text">I would describe myself as hard working, patient, creative, willing to
                            acquire new knowledge and someone who loves learning about the surrounding world. </p>

                    </section>

                    <section className="json-container">
                        <div className="image">
                            <figure>
                                <img className="my-face" src="http://via.placeholder.com/350x350" alt="Picture of myself."/>
                            </figure>

                        </div>
                        <div className="json">
                            <div className="json-item"> <span className="json-key">profession</span>: <span
                                className="json-value">full stack developer</span>,
                            </div>
                            <div className="json-item"> <span className="json-key">age</span>: <span
                                className="json-number">18</span>,
                            </div>
                            <div className="json-item"> <span className="json-key">hard-working</span>: <span
                                className="true"> true</span>,
                            </div>
                            <div className="json-item"> <span className="json-key">ambitions</span>: <span
                                className="json-value">huge</span>,
                            </div>
                            <div className="json-item"> <span className="json-key">quick learner</span>: <span
                                className="true"> true</span>,
                            </div>
                            <div className="json-item"> <span className="json-key">enthusiast</span>: <span
                                className="true"> true</span></div>
                        </div>
                    </section>
                </div>
            </main>
        );
    }

}

export default About;


