import React, {Component} from 'react';
import '../Styles/css/Skills.css';
import '../Styles/mfizz/font-mfizz.css';
import FilterButton from "./FilterButton";

class Skills extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeItem: "ADVANCED",
            sections: {
                ADVANCED: {
                    content: "Advanced knowledge means that I've made two or more projects with it and I'm really proficient in it.",
                    items: [
                        {class: "devicon-react-original-wordmark", name: "REACT", link:"https://reactjs.org/"},
                        {class: "devicon-javascript-plain", name: "JS (ES6), NODE.JS", link:"https://www.javascript.com/"},
                        {class: "devicon-git-plain", name: "GIT", link:"https://git-scm.com/"},
                        {class: "devicon-babel-plain", name: "BABEL", link:"https://babeljs.io/"},
                        {class: "devicon-webpack-plain", name: "WEBPACK", link:"https://webpack.js.org/"},
                        {class: "devicon-sequelize-plain", name:"Sequalize and TypeORM", link:"http://docs.sequelizejs.com/"},
                        {class: "devicon-d3js-plain", name:"D3.js", link:"https://d3js.org/"},
                        {class: "devicon-typescript-plain", name:"TypeScript", link:"https://www.typescriptlang.org/"},
                        {class: "devicon-express-original", name:"Express.js" , link:"https://expressjs.com/"}

                    ],
                    color: "#0b7c1c"
                },
                FAMILIAR: {
                    content: "Familiar knowledge means that I've made atleast one project with it and I know basics really well.",
                    items: [
                        {class: "devicon-java-plain", name: "JAVA", link:"https://www.java.com"},
                        {class: "icon-spring", name: "SPRING", link:"https://spring.io/"},
                        {class: "icon-unity", name: "UNITY", link:"https://unity3d.com/"},
                        {class: "devicon-nginx-original", name: "NGINX", link:"https://nginx.org/en/"},
                        {class: "devicon-mysql-plain", name: "MYSQL", link:"https://www.mysql.com/"},
                        {class: "devicon-python-plain", name: "PYTHON", link:"https://www.python.org/"},
                        {class: "devicon-ubuntu-plain", name: "UBUNTU", link:"https://www.ubuntu.com/"}
                    ],
                    color: "#5792f2"
                },
                LEARNING: {
                    content: "These are technologies that I am learning in my free time.",
                    items: [
                        {class: "icon-rust", name: "RUST", link:"https://www.rust-lang.org/pl-PL/"}
                    ],
                    color: "#ff7f00"
                }
            }
        };
        this.setFilter = this.setFilter.bind(this);
    }


    setFilter(activeItem) {
        this.setState({activeItem: activeItem});
    }


    parseItems() {
        const activeItem = this.state.activeItem;
        const color = this.state.sections[activeItem].color;
        return this.state.sections[activeItem].items.map((item) => <div key={item.name} data-color={color}
                                                                        className="skills-container-item">
            <span tabIndex={0} className={item.class}><a href={item.link}>{item.name}</a></span></div>);
    }


    render() {
        return (
                <main>
                    <div className="skills-container">
                        <section>
                            <h1>Skills and experience</h1>
                            <p className="skills-text">My skillset is oriented around front end web development and
                                everything
                                related with this side of web. I mostly work with <a className="react"
                                                                                     href="https://reactjs.org/">React</a> framework.
                            </p>
                            <p className="skills-text">I also have experience working on back end.My
                                recent projects were mostly made using the <a className="spring"
                                                                              href="https://spring.io/">Spring </a>framework.
                            </p>
                        </section>

                        <section className="skills-container-items-section">
                            <div
                                className="skills-container-description">{this.state.sections[this.state.activeItem].content} </div>
                            <ul>
                                <li>
                                    <FilterButton color="#0b7c1c"
                                                  isActive={this.state.activeItem === "ADVANCED"}
                                                  text="ADVANCED" setFilter={this.setFilter}/>
                                </li>
                                <li>
                                    <FilterButton color="#5792f2"
                                                  isActive={this.state.activeItem === "FAMILIAR"}
                                                  text="FAMILIAR"
                                                  setFilter={this.setFilter}/>
                                </li>
                                <li>
                                    <FilterButton color="#ff7f00"
                                                  isActive={this.state.activeItem === "LEARNING"}
                                                  text="LEARNING"
                                                  setFilter={this.setFilter}/>
                                </li>
                            </ul>
                            <div className="skills-container-list">
                                {this.parseItems()}
                            </div>

                        </section>
                    </div>
                </main>
        );
    }
}


export default Skills;


