import React, {Component} from 'react';
import '../Styles/css/Skills.css';

class FilterButton extends Component{
    constructor(props){
        super(props);
        this.state = { popoverVisible:false};
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(){
        this.props.setFilter(this.props.text);
    }



    render(){
        return(
            <div>
            <button  style={{borderColor: this.props.isActive ? this.props.color : '', color:this.props.isActive ? this.props.color : '' }} onClick={this.handleClick}>{this.props.text}</button>
            </div>
        );
    }
}

export default FilterButton;


